import { AppbarContainer, AppShell } from "@app/ui";
import { nextPreload, useTranslation } from "@fanfanlo";
import { useRouter } from 'next/router';
import { useEffect } from 'react';
import { RoomContent } from '@im/matrix';
import React from "react";

export default function RoomPage() {
  const { t } = useTranslation("homepage/pages/im/room/content");
  const router = useRouter();
  const { id: roomId } = router.query;

  useEffect(() => {
    nextPreload(router, '/im');
  }, []);
  useEffect(() => {
    // 如果没有roomId，重定向到聊天列表
    if (!roomId && router.isReady) {
      router.push('/im');
    }
  }, [roomId, router]);

  if (!roomId) {
    return (
      <AppShell>
        <AppbarContainer appbarProps={{ title: t("content.title"), back: true }} />
      </AppShell>
    );
  }
  return (
    
    <RoomContent roomId={roomId as string} />
  )

}